<docs>
  # HeroThematique
  > The CG hero thematique page
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, VueInstance } from "@vueuse/core";
import { store as themeStore } from "pages/Thematique/store";
import { interactWithSceneAt } from "plugins/webgl/three";
import { gsap } from "vendors/gsap";
import { defineComponent, toRefs } from 'vue';
import { STEPS } from "../webgl/IllustrationScene";

export default defineComponent({
  name: "HeroThematique",
  props: {
    title: {
      type: String,
      default: undefined,
    },
    subtitle: {
      type: String,
      default: undefined,
    },
    cta: {
      type: String,
      default: undefined,
    },
    variant: {
      type: String,
      default: "default"
    }
  },
  emits: ["discover"],
  setup(props, { emit }) {

    const el = templateRef<HTMLElement>("el");
    const discover = templateRef<VueInstance>("Discover");
    const title = templateRef<HTMLElement>("Title");
    const subtitle = templateRef<HTMLElement>("Subtitle");

    // Tween to display hero elements 
    const animateIn = () => {
      gsap.timeline()
        .add(() => {
          el.value.classList.remove("hidden")
        }, 0)
        .from(title.value, { scale: .9, opacity: 0, ease: "power4.inOut", duration: 1.5 }, 0)
        .from(subtitle.value, { yPercent: 50, opacity: 0, ease: "power4.out", duration: 1.5 }, .8)
        .from(discover.value?.$el, { yPercent: -50, opacity: 0, ease: "power4.out", duration: 1.5 }, .8);
    }

    // Tween to hide hero elements 
    const animateOut = () => {
      gsap.timeline()
        .to(el.value, { opacity: 0, clearProps: "all", ease: "power4.out", duration: .7 }, 0)
        .add(() => {
          el.value.classList.add("hidden")
        }, .5)
    }

    const onOver = () => {
      if (themeStore.isIntro) return;

      interactWithSceneAt(0, {
        callback: "draw",
        params: STEPS.introOver
      });
    }

    const onOut = () => {
      if (themeStore.isIntro) return;

      interactWithSceneAt(0, {
        callback: "draw",
        params: STEPS.intro
      });
    }

    const launchAnimation = () => {
      emit('discover');
    }

    return {
      ...toRefs(props),
      //
      animateIn,
      animateOut,
      onOver,
      onOut,
      launchAnimation
    };
  },
});
</script>

<template>
  <section ref="el"
           class="heroThematique hidden"
           :data-variant="variant"
           :data-dark="false"
  >
    <h3 ref="Subtitle"
        class="subtitle"
        v-html="subtitle"
    />
    <h1 ref="Title"
        class="title"
        v-html="title"
    />
    <ui-button ref="Discover"
               class="cta"
               theme="primary"
               :variant="variant"
               :label="cta"
               @mouseover="onOver"
               @mouseout="onOut"
               @click="launchAnimation"
    />
  </section>
</template>

<style lang="stylus" scoped>
  .heroThematique
    absolute top 50% left 50%
    size 100% auto
    f(center, $direction:column)
    transform translate(-50%, -50%)
    
    &.hidden
      pointer-events none
      opacity 0

    .cta
      rp(margin-top, 60px)
    
    .title
      f-style(h1)
      text-align center
      addVariantProps()

      :deep()
        .italic
          font-family $ff-title_italic
        .upper
          text-transform uppercase

    .subtitle
      rp(margin-bottom, 40px)
      f-style('text16')
      addVariantProps()
  </style>
