import { each, inRange, keys, map, random } from "lodash";
import { damp } from "maath/easing";
import emitter, { EVENTS } from 'store/events';
import { CompressedTexture, DoubleSide, Group, Mesh, MeshBasicMaterial, PlaneGeometry, Sprite, SpriteMaterial, Texture } from 'three';
import { WebGLRenderParams } from '../../gl';


/**
 * Default config if settings object is not defined
 */
const DEFAULT_CONFIG : WebGLSpritesheetSetting = {
  fps: 60,
  loop: true,
  wait: 0,
  autoplay:true,
  usePlane: false
}


const material = new SpriteMaterial({opacity:0, transparent:true});
const materialPlane = new MeshBasicMaterial({side:DoubleSide,opacity:0, transparent:true});

export class WebGLSpritesheetPlayer extends Group implements WebGLSpritesheetActions {
  config: WebGLSpritesheetConfig;
  name: string;

  player : Sprite | Mesh
  
  textures: Texture[] = []; // all textures
  textureIndex = 0; //current texture index in the textures array (in files -> the n image (10.png) (in json -> the n spritesheet image)

  textureSheets : WebGLSpritesheetJSON[] = []; // all json spritesheet objects
  textureSheetIndex = 0; // current json spritesheet object
  textureSheetsReachEnd = false; //reach the last frame of the last json spritesheet
  
  // Damped values to keep the fps independant
  textureIndexDamped = 0;
  waitDamped = 0;

  ready = false;
  seeking = false;
  playing = false;

  /**
   * Generate a spritesheet mesh
   * @param config Initial Config
   * @returns the instance
   */
  constructor(config:WebGLSpritesheetConfig) {
    super();

    this.name = `spritesheetmesh-${random(0, 1000)}`;
    this.config = {...config, path:{...config.path, prefix_filename: config.path.prefix_filename ?? ''}, settings: config.settings ?? DEFAULT_CONFIG };

    if(this.config.settings?.usePlane) {
      this.player = new Mesh(new PlaneGeometry(1,1), materialPlane.clone());
    }
    else{
      this.player = new Sprite(material.clone());
    }
    
    this.add(this.player);
    // this.preload();

    return this;
  }

  /**
   * Add the textures to the player
   * @param textures Textures[] or CompressedTexture[]
   */
  addTextures(textures:Texture[] | CompressedTexture[]):void {
    
    this.textures = textures;
    this.ready = true;

    emitter.emit(EVENTS.SPRITESHEET_READY);

    if(this.config?.settings?.autoplay) {
      this.play();
    }
  }

  /**
   * Start preload the image / json files
   */
  // async preload() {
  //   switch(this.config.format) {
  //     case "files":
  //       this.textures = await preloadImageSequence(this.config.total_frames, this.config.path, this.config.noKtx);
  //       break;
        
  //       case "json":
  //       const output = await preloadJSONSpritesheets(this.config.path); // eslint-disable-line no-case-declarations
  //       this.textures = output.textures;
  //       this.textureSheets = output.json;
  //       break;
  //   }
    
  //   this.ready = true;
    
  //   emitter.emit(EVENTS.SPRITESHEET_READY);

  //   // play it if autoplay
  //   if(this.config?.settings?.autoplay) {
  //     this.play();
  //   }
  // }
  
  /**
   * Resize the instance
   * @param scaleMultiplier Number to increase or decrease the scale while keeping the ratio
   */
  resize(scaleMultiplier?:number):void {

    const multiplier = scaleMultiplier ?? 1;

    if(this.config.ratio > 1) {
      this.scale.set(multiplier * (1 * this.config.ratio), 1 * multiplier, 1 * multiplier);
    }
    else{
      this.scale.set(1 * multiplier, (1 / this.config.ratio) * multiplier, 1 * multiplier);
    }
  }
  
  /**
   * Render the spritesheet
   * @param params Default WebGLRender Params
   * @returns 
   */
  render(params:WebGLRenderParams):void {  

    const mat = this.player.material as (SpriteMaterial | MeshBasicMaterial);
    const fps = this.config.settings?.fps ?? DEFAULT_CONFIG.fps!;
    const loop = this.config.settings?.loop ?? DEFAULT_CONFIG.loop!;
    const wait = this.config.settings?.wait ?? DEFAULT_CONFIG.wait!;
    const waitDamped = Math.floor(this.waitDamped);
    const multipleJSON = this.config.format === "json" ? this.config.path.json!.length > 1 : false; //We have multiple JSON or not
    const easing = (t: number) => 1 / (1 + t + 0.48 * t * t + 0.235 * t * t * t);
    const hasEvents = this.config.settings?.events !== undefined;

    // If we are in image sequence
    if(this.config.format === 'files') {
      mat.map = this.textures[this.textureIndex];
    }
    else{
      //otherwise it's JSON spritesheet
      //nothing before loading the JSON spritesheet
      if(!this.textureSheets.length) return;

      if(!this.textureSheetsReachEnd) {
        // getting the currentFrame of the animation
        let frameNames = keys(this.textureSheets[this.textureSheetIndex].frames);
        let currentFrameName = frameNames[this.textureIndex];
        
        // Go to the next JSON file and get the frameName
        if(!currentFrameName) {
          this.textureSheetIndex ++;
          
          // Reach End: Last frame from last JSON spritesheet
          if(this.textureSheetIndex === this.textureSheets.length) {
            this.textureSheetsReachEnd = true;
          }
          
          // Reset the frame index and continue with the next JSON spritesshet
          if(!this.textureSheetsReachEnd) {
            this.textureIndex = 0;
            this.textureIndexDamped = 0;
            
            frameNames = keys(this.textureSheets[this.textureSheetIndex].frames);
            currentFrameName = frameNames[this.textureIndex];
          }
        }
  
        // If we have a framename
        if(currentFrameName) {

          //search if there is an event
          if(hasEvents) {
            const events = keys(this.config.settings?.events);
            const frameEvent = events.indexOf(currentFrameName);
            if(frameEvent !== -1) {
              emitter.emit(EVENTS.SPRITESHEET_FRAME_EVENT, this.config.settings?.events![frameEvent])
            }
          }

          const currentTextureSheet = this.textureSheets[this.textureSheetIndex];
          
          // swap the material texture
          mat.map = this.textures[this.textureSheetIndex];
          
          // Update the offset and the repeat of the texture to get the right frame
          mat.map!.repeat.set(
            (currentTextureSheet.frames[currentFrameName].frame.w / currentTextureSheet.size.w),
            (currentTextureSheet.frames[currentFrameName].frame.h / currentTextureSheet.size.h)
          );
              
          mat.map!.offset.set(
            (currentTextureSheet.frames[currentFrameName].frame.x / currentTextureSheet.size.w),
            1 - (currentTextureSheet.frames[currentFrameName].frame.h / currentTextureSheet.size.h) - (currentTextureSheet.frames[currentFrameName].frame.y / currentTextureSheet.size.h)
          );
        }
      }
    }
    
    // Flip the player if texture is compressed
    if(mat.map && mat.map.userData.compressed) {
      if(this.player.scale.y > 0) this.player.scale.y = -1;
    }
    
    /**
     * Playing and Wait behavior
     */
    if(this.playing) {

      // damping
      if(fps === 'off') {
        this.textureIndexDamped += 1;
      }
      else{
        damp(this, 'textureIndexDamped', this.textureIndexDamped + 1, 1/(fps as number), params.delta, Infinity, easing, 0.0001);
      }
    
      // If our floored damping value is different than our current texture index (ex 2 != 1)
      if(Math.floor(this.textureIndexDamped) !== this.textureIndex) {
        this.textureIndex = Math.floor(this.textureIndexDamped);

        // Reach the end of the frame
        if(this.textureIndex === this.config.total_frames -1 || this.textureSheetsReachEnd) {

          emitter.emit(EVENTS.SPRITESHEET_LAST_FRAME, this.config.id);

          if(loop) {
            // do we have to wait?
            if(wait > 0) {
              this.pause();
            }
            else{
              if(multipleJSON) {
                this.textureSheetIndex = 0;
                this.textureSheetsReachEnd = false;
              }
              
              this.textureIndex = 0;
              this.textureIndexDamped = 0;
            }
          }
          else{
            //pause if not looping
            this.pause();
          }
        }
      }
    }
    else {
      // We damp the wait value
      if(wait > 0 && waitDamped !== wait) {
        if(fps === 'off') {
          this.waitDamped += 1;
        }
        else{
          damp(this, 'waitDamped', this.waitDamped + 1, (fps === 'off') ? 0 : 1/(fps as number), params.delta, Infinity, easing, 0.0001);
        }
      }
      else{
        if(multipleJSON) {
          if(!this.seeking) this.textureSheetIndex = 0;
          this.textureSheetsReachEnd = false;
        }
        
        if(!this.seeking) this.textureIndex = 0;
        this.textureIndexDamped = 0;
        this.waitDamped = 0;

        if(loop && this.config.settings?.autoplay) this.play();
      }
    }

    //require to tell threejs that we updated the texture
    mat.needsUpdate = true;
  }
  
  /**
   * Release all textures from memory
   */
  dispose():void {
    this.player?.geometry?.dispose();
    
    const mat = this.player.material as (SpriteMaterial | MeshBasicMaterial);
    mat.map?.dispose();
    mat.dispose();

    each(this.textures, tex => {
      tex?.dispose();
    })

    this.textures = [];
    this.textureSheets = [];
  }

  /**
   * Play: Set the playing flag to true
   */
  play() {
    this.playing = true;
    this.seeking = false;
    if(!this.material.opacity) this.material.opacity = 1;
  }

  /**
   * Pause: Set the playing flag to false
   */
  pause() {
    this.playing = false;
  }

  /**
   * Go to a specific frame based on ratio
   * @param frameNumber
   */
  seek(frameNumber:number) {
    if(this.config.format === 'files') {
      this.textureIndex = frameNumber;
    }
    else{
      if(this.config.path.json?.length === 1) {
        this.textureIndex = frameNumber;
      }
      //multiple JSON
      else{
        const frames = map(this.textureSheets, (sheet:WebGLSpritesheetJSON) => {
          return keys(sheet.frames).length;
        });

        //find the right textureSheetIndex first
        let index = 0;
        let frameIndex = 0;
        let totalParsed = 0

        each(frames, (total, i) => {
          if(inRange(frameNumber, totalParsed, totalParsed + total)) {
            index = i
            frameIndex = frameNumber - totalParsed;
          }
          totalParsed += total;
        });

        this.textureSheetIndex = index;
        this.textureIndex = frameIndex;
      }
    }

    // limits
    if(this.textureSheetIndex === this.textureSheets.length) this.textureSheetIndex = this.textureSheets.length - 1;
    if(this.textureIndex === this.textures.length) this.textureIndex = this.textures.length - 1;

    this.seeking = true;
    
    this.render({delta:0,time:0});
  }

  get material() {
    return this.player.material as (SpriteMaterial | MeshBasicMaterial);
  }
}
