import art from "./art";
import charles from "./charles";
import festival from "./festival";
import maria from "./maria";
import metier from "./metier";
import raretes from "./raretes";
import university from "./university";
import yns from "./yns";

export const THEMES_SVGS = {
  yns,
  maria,
  art,
  charles,
  festival,
  metier,
  university,
  raretes
} as const;

export const getSVGConfig = (id:string) : ThemeSVG => {
  switch(id) {
    default:
    case "yns":
      return THEMES_SVGS.yns;
    
    case "art":
      return THEMES_SVGS.art;
    
    case "charles":
      return THEMES_SVGS.charles;

    case "festival":
      return THEMES_SVGS.festival;

    case "metier":
      return THEMES_SVGS.metier;

    case "university":
      return THEMES_SVGS.university;

    case "rarete":
      return THEMES_SVGS.raretes;

    case "maria":
      return THEMES_SVGS.maria;
  }
}
