<docs>
# PageThematique
> The CG thematique page

@author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, tryOnUnmounted, until, VueInstance } from '@vueuse/core';
import ArrowSVG from "assets/svg/arrow.svg?raw";
import axios from 'axios';
import { store as themeStore } from 'pages/Thematique/store';
import emitter, { EVENTS } from 'plugins/store/events';
import { clearWebGL, interactWithSceneAt, useWebGL } from 'plugins/webgl/three';
import { store as appStore } from 'store/app';
import { store as loaderStore } from 'ui/Loader/store';
import SoundButton from "ui/SoundButton.vue";
import usePageFlow from 'utils/flow/page';
import { playSound, stopSound } from 'utils/helpers/Sound';
import { gsap } from "vendors/gsap";
import { computed, defineComponent, onBeforeMount, provide, ref, toRefs, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute, useRouter } from 'vue-router';
import Hero from './blocks/Hero.vue';
import ListingBlock from './blocks/Listing.vue';
import { STEPS } from './webgl/IllustrationScene';
import { getSVGConfig } from './webgl/config';

export interface VueAnimate extends VueInstance {
  animateIn(): void;
  animateOut(): void;
}

export default defineComponent({
  name: "PageThematique",
  components: {
    Hero,
    ListingBlock,
    SoundButton,
  },
  setup() {
    const { t } = useI18n();
    const route = useRoute();
    const router = useRouter();
    const Scene = templateRef<HTMLElement>("Scene");
    const Hero = templateRef<VueAnimate>("Hero");
    const Listing = templateRef<VueAnimate>("Listing");
    const disabled = computed(() => appStore.disabled);
    const { done } = toRefs(loaderStore.state);
    const heroVisible = ref(true);
    const soundButtonVisible = ref(false);

    //check if the thematique page is in the store (exists)
    const isValidThematique = themeStore.findTheme(`/${route.params.slug as string}/`);

    const { readyToRender, getData, onInitUi } = usePageFlow<PageThematique>({
      load() {

        if (!isValidThematique) {
          router.push('/');
          return Promise.resolve();
        }

        return axios.get(`/thematiques/${route.params.slug}/data.json`)
          .then(({ data }) => {
            themeStore.update(data);
            themeStore.setCurrent(data.id);
            return {
              ...data
            }
          })
      },
      ui: {
        illustration: () => import("./webgl/IllustrationScene"),
      }
    });

    // ---- Watch if we are in a page transition
    watch(
      () => appStore.isAPageTransition,
      (path) => {
        if (path !== undefined) {
          animateLeaveOut();
        }
      }
    )

    onBeforeMount( () => {
      if(!appStore.introSeen && appStore.redirection) {
        router.push("/");
        return;
      }
    });

    onInitUi(async ({ ui }) => {
      if (!disabled.value) appStore.disableApp(true);

      // Retrieve the svg config
      const SVGConfig = getSVGConfig(page.value.id);

      await until(done).toBe(true);

      useWebGL(
        [
          {
            glScene: ui.illustration.default,
            settings: {
              config: SVGConfig,
            }
          },
        ], { antialias: true, alpha: true, canvasContainer: Scene.value, clearForMobile: true });

      // set the background color
      if (themeStore.current) {
        appStore.setAppBgColor(SVGConfig?.bgColor);
        themeStore.setNextVariant();
      }

      // animation
      const cameFromListing = themeStore.fromListing;
      if (!cameFromListing) {
        animateIn();

        //add Events
        emitter.on(EVENTS.THEME_SWITCHING, onSwitchTheme);
        emitter.on(EVENTS.HIDE_SPRITESHEET, onHideIntro);
        emitter.on(EVENTS.SHOW_LISTING, onShowListing);
      }
      else {
        animateInFromListing();
      }
    });

    // Leaving the page - Clearing 
    tryOnUnmounted(() => {
      themeStore.toggleIntro(false);
      themeStore.toggleIsListing(false);
      themeStore.toggleThemeHeader(false);
      clearWebGL(true);

      //add Events
      emitter.off(EVENTS.THEME_SWITCHING, onSwitchTheme);
      emitter.off(EVENTS.HIDE_SPRITESHEET, onHideIntro);
      emitter.on(EVENTS.SHOW_LISTING, onShowListing);
    })

    // Animation Transition In
    const animateIn = () => {
      gsap.timeline()
        .add(() => {
          interactWithSceneAt(0, {
            callback: "draw",
            params: STEPS.intro
          });
        }, .1)
        .add(() => {
          Hero.value?.animateIn();
        }, .5)
        .add(() => {
          themeStore.toggleThemeHeader(true);
        }, 1)
        .add(() => {
          appStore.disableApp(false);
        }, 1.6);
    }

    const animateInFromListing = () => {

      emitter.emit(EVENTS.SHOW_LISTING);
      themeStore.toggleThemeHeader(true); // to display the header
      
      gsap.timeline()
        .add(() => {
          interactWithSceneAt(0, {
            callback: "leave",
          });
          themeStore.toggleIntro(true);
          heroVisible.value = false;
        }, .1)
        .add(() => {
          themeStore.toggleFromListing(false);
        }, .5)
        .add(() => {
          appStore.disableApp(false);
        }, 1.5);
    }

    // Animation Transition Out
    const animateOut = () => {
      gsap.timeline()
        .add(() => {
          interactWithSceneAt(0, {
            callback: "draw",
            params: STEPS.default
          });
          //hide hero
          Hero.value?.animateOut();
        }, 0)
    }

    // Animation Transition Out (Only for page transition)
    const animateLeaveOut = () => {
      appStore.disableApp(true);

      gsap.timeline()
        .add(() => {
          if (heroVisible.value) {
            interactWithSceneAt(0, {
              callback: "leave"
            });

            //hide hero
            Hero.value?.animateOut();
          }
          else {
            //hide listing
            Listing.value?.animateOut();
          }
        }, 0)
        .add(() => {
          appStore.disableApp(false);

          const nextRoute = appStore.isAPageTransition;
          appStore.setPageTransition(undefined);

          if (nextRoute) router.push(nextRoute);
        }, 1.3);
    }

    // When we enter a thematique - transition to draw the line
    const onDiscover = () => {
      themeStore.toggleIntro(true);
      soundButtonVisible.value = true;

      Hero.value?.animateOut();
      themeStore.toggleThemeHeader(false);

      interactWithSceneAt(0, {
        callback: "draw",
        params: STEPS.illustrationBefore
      });

      //play the sound
      playSound(`${page.value.id}-track`);
    }

    // When we switch theme - needs to to the transition between themes
    const onSwitchTheme = () => {
      // disabling
      appStore.disableApp(true);
      animateOut();

      stopSound(`${page.value.id}-track`);

      themeStore.toggleIntro(false);
    }

    // The Listing will be shown in a few seconds
    const onShowListing = () => {
      soundButtonVisible.value = false;
    }

    // Hide the intro
    const onHideIntro = () => {
      interactWithSceneAt(0, {
        callback: "clear"
      });

      heroVisible.value = false;

      themeStore.setIllustrationSeen(page.value.id);

      clearWebGL(true);
    }

    const onSkip = () => {
      themeStore.toggleFromListing(true);
      themeStore.setIllustrationSeen(page.value.id);
      
      interactWithSceneAt(0, {
        callback: "skip"
      });
    }

    const page = getData();
    provide("page", page);

    return {
      t,
      readyToRender,
      page,
      disabled,
      onDiscover,
      onSwitchTheme,
      heroVisible,
      soundButtonVisible,
      onSkip,
      hasSeen: computed(() => themeStore.hasSeenIllu(page.value.id)),
      isIntro: computed(() => themeStore.isIntro),
      isListing: computed(() => themeStore.isListing),
      ArrowSVG,
    };
  },
});
</script>

<template>
  <main v-if="readyToRender"
        ref="el"
        :key="page.id"
        :data-variant="page.id"
        :data-disabled="disabled"
        :data-listing="isListing"
        :data-seen="hasSeen"
        class="page thematique"
  >
    <Hero v-if="heroVisible"
          v-bind="page"
          ref="Hero"
          :variant="page.id"
          @discover="onDiscover"
    />
    <div ref="Scene" class="scene" />
    <SoundButton :noinvert="true"
                 :theme="page.id"
                 :data-hidden="!isIntro || !soundButtonVisible"
    />
    <ui-button v-if="isIntro && hasSeen && !isListing"
               :key="`header-thematique-${isIntro}`"
               :label="t('LABELS.skip')"
               theme="primary"
               class="skip"
               :icon="ArrowSVG"
               tag="a"
               :variant="page.id"
               @click="onSkip"
    />
    <ListingBlock ref="Listing" :variant="page.id" />
  </main>
</template>

