import { WebGLRenderParams, WebGLSceneOptions, WebGLSize } from "components/WebGL/gl";
import { WebGLScene } from 'components/WebGL/three/glScene';
import { WebGLSpritesheetPlayer } from "components/WebGL/three/ui/glSpritesheetPlayer";
import { each, findIndex, map } from "lodash";
import { damp } from "maath/easing";
import { MeshLineGeometry, MeshLineMaterial } from 'meshline';
import emitter, { EVENTS } from "plugins/store/events";
import { mainWebGLRenderer } from "plugins/webgl/three";
import { calculateUnitSize, transform2DPointTo3D } from "plugins/webgl/utils";
import { store as interfaceStore } from 'store/interface';
import { Box3, Group, Mesh, PerspectiveCamera, SRGBColorSpace, Vector2, Vector3 } from "three";
import { SVGLoader, SVGResultPaths } from 'three/examples/jsm/loaders/SVGLoader.js';
import { store as loaderStore } from 'ui/Loader/store';
import { stopSound } from "utils/helpers/Sound";
import { gsap } from "vendors/gsap";

export const STEPS = {
  default: "default",
  intro: "intro",
  introOver: "introOver",
  illustrationBefore: "illustrationBefore",
  illustrationAfter: "illustrationAfter",
  end: "end"
}

const DAMP_EASING = (t: number) => 1 / (1 + t + 0.48 * t * t + 0.235 * t * t * t);
const PTS_QUALITY = 100;

let RAF : number;

export default class extends WebGLScene {
 
  container : Group;
  sceneSize : WebGLSize;

  svgData : SVGResultPaths[] = []; 
  svgConfig : ThemeSVG;
  lines : Mesh[] = [];
  linesRatio: number[] = [];
  lineMaterial : MeshLineMaterial;

  currentStep : string = STEPS.default;
  isAnimating : boolean = false;
  isTravelling : boolean = false;

  spritesheet : WebGLSpritesheetPlayer;

  handlers : Obj = {};
 
  constructor(options:WebGLSceneOptions) {
    super(options);

    this.svgConfig = this.settings?.config as ThemeSVG;
    this.container = new Group();
    this.sceneSize = calculateUnitSize(this.camera.position.z - this.container.position.z, this.getActiveCamera() as PerspectiveCamera);
    
    this.lineMaterial = new MeshLineMaterial({
      resolution:new Vector2( mainWebGLRenderer!.domElement.offsetWidth, mainWebGLRenderer!.domElement.offsetHeight),
      lineWidth: this.svgConfig.svg.lineWidth,
      dashArray: 1,
      dashOffset: 1,
      dashRatio: 1,
      opacity: 1,
      color: this.svgConfig.svg.color
    });
    this.lineMaterial.transparent = true;

    this.handlers.spritesheetEnd = this.onSpritesheetEnd.bind(this);
    this.handlers.raf = this.watchSpritesheetLoading.bind(this);

    //---- Init the spritesheet
    this.spritesheet = new WebGLSpritesheetPlayer(this.svgConfig.spritesheet);
    this.spritesheet.name = "spritesheet";
    this.spritesheet.visible = false;

    //add the textures
    const textures = loaderStore.getSpritesheet(`${this.svgConfig.spritesheet.id}-spritesheet`);
    this.spritesheet.addTextures(textures);
    
    emitter.on(EVENTS.SPRITESHEET_LAST_FRAME, this.handlers.spritesheetEnd);

    return this;
  }

  build() {
    this.scene.add(this.container);

    this.svgData = loaderStore.getSvg(`${this.svgConfig.spritesheet.id}-svg`).paths;
    this.update();
  }

  update() {
    while(this.container.children.length) {
      const m = this.container.children[0] as Mesh;
      if(m && m.name !== "spritesheet") {
        m.geometry?.dispose();
        (m.material as MeshLineMaterial)?.dispose();
      }

      this.container.remove(this.container.children[0]);
    }

    this.container.scale.set(1,1,1);
    this.container.position.set(0,0,0);
    this.container.clear();
    this.lines = []

    each(this.svgData, (path : SVGResultPaths, index:number) => {
      
      const shapes = SVGLoader.createShapes(path);
      let computePoints : Vector2[] = [];
      
      each(shapes, (shapePath) => {
        const points = map(shapePath.getPoints(PTS_QUALITY), (pt) => {  
          return this.mapPointTo3D(pt);
        });
        
        computePoints = computePoints.concat(points);
      });

      const geometry = new MeshLineGeometry();
      geometry.setPoints(computePoints);
      
      const material = this.lineMaterial.clone();
      if(index < this.linesRatio.length && this.linesRatio[index]) {
        material.dashRatio = this.linesRatio[index]; // to avoid redraw everything on resize
      }

      const mesh = new Mesh(geometry, material);
      mesh.name = `line_${index}`;
      
      // Compute the bounding box of the shape
      const box = new Box3().setFromObject(mesh);
      const shapeCenter = box.getCenter(new Vector3());
      
      // Offset the mesh to center it in the group
      mesh.position.sub(shapeCenter);  // Move the shape to center it
      
      // Debug - Optional (if the lines are not aligned)
      const debug =  this.svgConfig.svg.offsets?.[index] || { x:0, y:0 };  
      if(debug.color) (mesh.material as MeshLineMaterial).color.setStyle(debug.color, SRGBColorSpace);
      mesh.visible = debug.visible === undefined ? true : debug.visible;
      
      //exception for oeuvre preferes
      if(this.svgConfig.spritesheet.id === 'art' && interfaceStore.highResVersion) {
        if(index === 1) debug.y = -0.01; 
        if(index === 2) {
          debug.x = -0.025
          debug.y = -0.03
        } 
      }

       //exception for raretes
       if(this.svgConfig.spritesheet.id === 'rarete' && interfaceStore.highResVersion) {
        if(index === 1) {
          debug.x = -0.01;
          debug.y = 0.0;
        }
      }

      //exception for metier
      if(this.svgConfig.spritesheet.id === 'metier' && interfaceStore.highResVersion) {
        if(index === 1) {
          debug.x = -0.018
          debug.y = 0.0;
        }
      }

      mesh.position.x += debug.x * (this.sceneSize.width);
      mesh.position.y += debug.y * (this.sceneSize.width);

      this.container.add(mesh);
      this.lines.push(mesh);
    });
   
    //--- Scaling
    const svgWidthUnit = (this.svgConfig.svg.width / mainWebGLRenderer!.domElement.clientWidth) * this.sceneSize.width;
    const scaleFactor = (this.sceneSize.width * 2.5) / svgWidthUnit; 
    this.container.scale.set(scaleFactor, scaleFactor, scaleFactor);

    const box = new Box3().setFromObject(this.container);
    const size = box.getSize(new Vector3());

    this.container.position.x = (size.x / 2) - (this.sceneSize.width / 2);
    this.container.position.y = 0;

    // add the spritesheet    
    if(!this.spritesheet.parent) {
      this.spritesheet.renderOrder = 1;
      this.scene.add(this.spritesheet);
    }
      
    // Scale the Spritesheet
    const planeWidthUnit = (this.svgConfig.spritesheet.width / mainWebGLRenderer!.domElement.clientWidth) * this.sceneSize.width;
    const baseScale = planeWidthUnit * scaleFactor;
    const planeHeight = baseScale / this.svgConfig.spritesheet.ratio;
    this.spritesheet.scale.set(baseScale, planeHeight, 1);

    
    if(this.svgConfig.svg.globalOffset) {
      this.container.position.x += (this.svgConfig.svg.globalOffset.x * this.sceneSize.width) || 0;
      this.container.position.y += (this.svgConfig.svg.globalOffset.y * this.sceneSize.height) || 0;
    }

    if(this.svgConfig.spritesheet.globalOffset) {
      this.spritesheet.userData.x = (this.svgConfig.spritesheet.globalOffset.x * this.sceneSize.width) || 0;
      this.spritesheet.userData.y = (this.svgConfig.spritesheet.globalOffset.y * this.sceneSize.height) || 0;
    }
  }

  // Resize is called by default (by the plugin) when the scene is created
  resize() {
    super.resize();

    this.sceneSize = calculateUnitSize(this.camera.position.z - this.container.position.z, this.getActiveCamera() as PerspectiveCamera);
    this.lineMaterial.resolution.set( mainWebGLRenderer!.domElement.offsetWidth, mainWebGLRenderer!.domElement.offsetHeight);

    // If no children in the scene, let's build it
    if(!this.getActiveScene().children.length) {
      this.build();
    }
    else if(this.svgData.length) {
      this.update();
    }
  }

  render(params:WebGLRenderParams) {
    super.render(params);

    const precision = this.currentStep !== STEPS.illustrationBefore ? 0.0001 : 0.001;
    
    if(this.isAnimating) {

      const randomsX = [0.02, -0.01, 0.015];
      const randomsY = [-0.015, 0.01, 0.02];

      each(this.lines, (mesh, index) => {
        const material = mesh.material as MeshLineMaterial;
        damp(material, 'dashRatio', this.svgConfig.svg.steps[this.currentStep].dash, this.svgConfig.svg.steps[this.currentStep].speed, params.delta, Infinity, DAMP_EASING, precision);
  
        if(!mesh.userData.position) {
          mesh.userData.position = mesh.position.clone();
        }
        else{
          mesh.position.x = mesh.userData.position.x + Math.cos(params.time * 2) * randomsX[index];
          mesh.position.y = mesh.userData.position.y + Math.sin(params.time * 2) * randomsY[index];;
        }

        // Change Step during the travelling
        if(!index) {
          if(this.currentStep === STEPS.illustrationBefore) {
            const diff = material.dashRatio - this.svgConfig.svg.steps[this.currentStep].dash;
            if(diff < 0.03) {
              this.draw(STEPS.illustrationAfter);
            }
          }

          if(this.currentStep === STEPS.illustrationAfter) {
            const diff = material.dashRatio - this.svgConfig.svg.steps[this.currentStep].dash;
            if(diff < 0.1) {
              this.draw(STEPS.end);
            }
          }
  
          // Swap to the animation
          if(this.currentStep === STEPS.end && this.svgConfig.svg.maxDiff) {
            const diff = material.dashRatio - this.svgConfig.svg.steps[this.currentStep].dash;
            if(diff < this.svgConfig.svg.maxDiff) {
              this.swapToSpritesheet();
            }
          }
        }
  
        this.linesRatio[index] = material.dashRatio;
      });
    }

    if(this.isTravelling) {
      const offsetValue = this.svgConfig.spritesheet.globalOffset?.sceneOffset || 0.15;
      const target = this.currentStep === STEPS.illustrationBefore ? 0 : -(this.sceneSize.width * offsetValue);
      const speedRatio = this.currentStep === STEPS.illustrationBefore ? 1 : 0.5;
      damp(this.camera.position, 'x', (this.sceneSize.width * 1.5) - target, this.svgConfig.svg.steps[this.currentStep].speed * speedRatio, params.delta, Infinity, DAMP_EASING, precision);
    }
    
    // --- Spritesheet
    if(this.spritesheet.visible) {
      this.spritesheet?.render(params);
    }
    this.spritesheet.position.x = this.camera.position.x + this.spritesheet.userData.x;
    this.spritesheet.position.y = this.spritesheet.userData.y;
  }

  clear() {
    emitter.off(EVENTS.SPRITESHEET_LAST_FRAME, this.handlers.spritesheetEnd);
    
    each(this.lines, line => {
      line.geometry?.dispose();
      (line.material as MeshLineMaterial)?.dispose();
      line.clear();
    });
    
    this.lineMaterial.dispose();
    
    this.svgData = []; 
    this.container.clear();
    this.handlers = {};
    this.spritesheet?.clear();
  }

  dispose() {
    this.clear();
    this.disposeGeometries(this.container);
    this.spritesheet?.dispose();
    super.dispose();
  }

  /**
   * Map a 2D point to a 3D point
   * @param v Vector2
   */
  mapPointTo3D(v : Vector2) {
    const v3d =  transform2DPointTo3D(v, {
      width: mainWebGLRenderer!.domElement.clientWidth,
      height: mainWebGLRenderer!.domElement.clientHeight
    })

    return new Vector2((this.sceneSize.width / 2) * v3d.x, (this.sceneSize.height / 2) * v3d.y);
  }

  /**
   * Draw the scene based on the step
   * @param id string
   */
  draw(id:string) {

    this.currentStep = id;

    //add the textures
    const notEnoughTexture = findIndex(this.spritesheet.textures, (t) => t === undefined);    
    if(notEnoughTexture !== -1) {
      
      if(!loaderStore.isSpritesheetLoading) {
        loaderStore.finishSpriteSheetsLoading();
      }
      
      this.watchSpritesheetLoading();
      return;
    }


    this.isAnimating = true;
    this.isTravelling = id === STEPS.illustrationBefore || id === STEPS.illustrationAfter || id === STEPS.end;
  }

  skip() {
    if(this.spritesheet.playing) this.spritesheet.pause();
    this.isAnimating = this.isTravelling = false;
    this.onSpritesheetEnd(true);

     each(this.lines, line => {
        const mat = (line.material as MeshLineMaterial);
        gsap.to(mat, {opacity:0, duration:.5, ease:"power4.out"});
      });
  }

  /**
   * Leave the scene
   */
  leave() {
    // hide the spritesheet if it's playing
    if(this.spritesheet.playing) {
      gsap.to(this.spritesheet.material, {opacity:0, ease:"expo.out", duration:1}); 
    }
    else{
      //or rewind the line
      this.currentStep = STEPS.default;
      this.isAnimating = true;
      this.isTravelling = false;
    }
  }

  /**
   * Swap the SVG to the Spritesheet
   */
  swapToSpritesheet() {
    if(this.spritesheet.visible) return;

    this.spritesheet.visible = true;
    this.spritesheet.play();
    
    gsap.timeline()
      .from(this.spritesheet.material, {opacity:0, ease:"power4.out", duration:this.svgConfig.spritesheet.swapSpeed}, 0)
      .add(() => {
        each(this.lines, line => {
          const mat = (line.material as MeshLineMaterial);
          gsap.to(mat, {opacity:0, duration:this.svgConfig.spritesheet.swapSpeed, ease:"power4.out"});
        });
      }, 0)
      .add( () => {
        this.container.visible = false;
      }, this.svgConfig.spritesheet.swapSpeed + .05)

  }

  /**
   * When the spritesheet ends
   */
  onSpritesheetEnd(skipping = false) {
    emitter.emit(EVENTS.SHOW_LISTING);
    emitter.off(EVENTS.SPRITESHEET_LAST_FRAME, this.handlers.spritesheetEnd);

    stopSound(`${this.svgConfig.spritesheet.id}-track`, skipping ? 500 : 3500);
  }

  /**
   * Watch if all spritesheet textures have been loaded
   */
  watchSpritesheetLoading() {
    const textures = loaderStore.getSpritesheet(`${this.svgConfig.spritesheet.id}-spritesheet`);
    const notEnoughTexture = findIndex(textures, (t) => t === undefined);    
    
    if(notEnoughTexture === -1) {
      cancelAnimationFrame(RAF);
      this.spritesheet.addTextures(textures);
      this.draw(this.currentStep);
    }
    else{
      RAF = requestAnimationFrame(this.handlers.raf);
    }

  }
}

